import { Injectable } from '@angular/core';
import { Book } from './books/book';
import { Observable, throwError} from 'rxjs';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';


@Injectable({
  providedIn: 'root'
})

export class BookDataService {

  constructor(private http: HttpClient) { }
    private errorHandler(error: HttpErrorResponse): Observable<any> {
      console.error('Fehler aufgetreten!' + error); return throwError(error);
  }

  getBooks(): Observable<Book[]> {
    return this.http.get<Book[]>(environment.baseUrl+"/books"
    ).pipe(
      catchError(this.errorHandler)
    )
  }

  findBooks(term: String): Observable<Book[]>{
    return this.http.get<Book[]>(environment.baseUrl+"/books/regex?term="+term
    ).pipe(
      catchError(this.errorHandler)
    )
  }

  getBookByISBN(isbn: String): Observable<Book>{
     return this.http.get<Book>(environment.baseUrl+"/books/"+isbn
    ).pipe(
      catchError(this.errorHandler)
    )
  }

  update(book: Book): Observable<any> {
    return this.http.put( environment.baseUrl+"/books", book, { responseType: 'text' }
    ).pipe(
      catchError(this.errorHandler)
    );
  }
  updateBookByISBN(book: Book): Observable<any>{
    return this.http.put(environment.baseUrl+"/books/"+book.isbn, book, {responseType: 'text'}
    ).pipe(
      catchError(this.errorHandler)
    )

  }

  add(book: Book): Observable<any> {
    return this.http.post(environment.baseUrl+"/books", book, { responseType: 'text' }).
        pipe(catchError(this.errorHandler)
      );
    }

  remove(isbn: string): Observable<any> {
    return this.http.delete( environment.baseUrl+"/books/"+isbn, { responseType: 'text' }
    );
  }



}
