import { Component, OnInit } from '@angular/core';
import { Book } from '../book';
import { ActivatedRoute } from '@angular/router';
import { BookDataService } from 'src/app/book-data.service';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-book-add',
  templateUrl: './book-add.component.html',
  styleUrls: ['./book-add.component.scss']
})
export class BookAddComponent implements OnInit {

  book: Book = undefined;
  bookCopy: Book = undefined;
  message = "";
  sub: Subscription = new Subscription();


  constructor(private route: ActivatedRoute, private bookData: BookDataService) {
    this.book = {
      "_id":"",
      "title": "",
      "subtitle": "",
      "isbn": "",
      "abstract1": "",
      "numPages": 0,
      "author": "",
      "publisher": {
        "name": "",
        "url": ""
      },
      "cover": "",
      "keywords":[""]
    }
  }

  ngOnInit(): void {
  }

  add() {
    // ignores input because it is already on book - this method would need parameter for a reactive form
    this.bookCopy = {...this.book}
    this.book = undefined
    this.sub.add(
      this.bookData.add(this.bookCopy).subscribe(
        () => {this.updateCallBack()}
      )
    );
  }

  updateCallBack(){
    this.message = "Saved!"
    setTimeout(()=>{this.message=""}, 3000)

  }

  ngOnDestroy(): void {
    if (this.sub){
      this.sub.unsubscribe();
    }
  }
}
