import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import {FormBuilder, FormGroup, FormArray, FormControl, Validators} from '@angular/forms'
import { Book } from '../book';

@Component({
  selector: 'app-book-search',
  templateUrl: './book-search.component.html',
  styleUrls: ['./book-search.component.scss']
})

export class BookSearchComponent implements OnInit {

  bookForm: FormGroup;
  @Output() submitBook = new EventEmitter<Book>();

  constructor(private fb: FormBuilder) {
  }

  ngOnInit(): void {
    this.initForm();
    this.bookForm.valueChanges.subscribe(groupValue => console.log(groupValue));

  }

  private initForm() {
    if (this.bookForm) { return; }
    this.bookForm = this.fb.group({
      title: ['', Validators.required],
      subtitle: [''],
      isbn: ['', [ Validators.required, Validators.minLength(10), Validators.maxLength(13) ]],
      description: [''],
      authors: this.buildAuthorsArray(['']),
      thumbnails: this.buildThumbnailsArray([ { title: '', url: '' } ]),
      published: [] });

  }

  private buildAuthorsArray(values: string[]): FormArray {
      return this.fb.array(values, Validators.required);
  }

  private buildThumbnailsArray(values: any[]): FormArray {
    return this.fb.array( values.map(t => this.fb.group(t)));
  }

  get authors(): FormArray {
    return this.bookForm.get('authors') as FormArray;
  }

  get thumbnails(): FormArray {
        return this.bookForm.get('thumbnails') as FormArray;
  }

  addAuthorControl() {
    this.authors.push(this.fb.control(''));
    }
  addThumbnailControl() { this.thumbnails.push( this.fb.group({ url: '', title: '' })
    );
    }


  submitForm(){
    console.log(this.bookForm.value);
    this.bookForm.reset();


  }
}
