import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-mouse-cursor',
  templateUrl: './mouse-cursor.component.html',
  styleUrls: ['./mouse-cursor.component.scss']
})
export class MouseCursorComponent implements OnInit {

  // style underscore javascript wird weiter so verwende tauch wenn ts
  private _x =  -1; // type-inference besser
  private _y: number; // alternativ

  // getter leichtgewichtig halten wegen performance
  get x(): number{
    return this._x
  }

  get y(): number{
    return this._y
  }
  constructor() { }

  ngOnInit(): void {
  }

  onMouseMove(e: MouseEvent){
    this._x = e.clientX;
    this._y = e.clientY;
    // console.log(this.x, this.y)
  }

}
