import { Component, OnInit } from '@angular/core';
import {TitleBoxType} from './title-box/title-box.component'

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  ngOnInit(): void {
    setTimeout(()=> {this.titleType = TitleBoxType.SUCCESS}, 2000);
  }
  title = 'bookmanager';
  anyVariable = "Hallo Workshop"
  titleType: TitleBoxType  = TitleBoxType.ERROR

  fn(e: Event) {
    console.log("Title has been clicked with event " + e )
  }
}
