import { Component, OnInit, NgModule, Output, EventEmitter } from '@angular/core';
import { FormsModule } from '@angular/forms'
import { Book } from '../book';
import {ActivatedRoute} from '@angular/router';
import { BookDataService } from 'src/app/book-data.service';
import { Observable, Subscription } from 'rxjs';



@Component({
  selector: 'app-book-edit',
  templateUrl: './book-edit.component.html',
  styleUrls: ['./book-edit.component.scss']
})


export class BookEditComponent implements OnInit {

  book: Book = undefined;
  message = ""
  sub: Subscription = new Subscription();

  selectedOptions = [];
  options = ["key1", "key2", "key3", "key4"];

  config = {
    displayKey:"description", //if objects array passed which key to be displayed defaults to description
    search:true ,//true/false for the search functionlity defaults to false,
    height: 'auto', //height of the list so that if there are more no of items it can show a scroll defaults to auto. With auto height scroll will never appear
    placeholder:'Select', // text to be displayed when no item is selected defaults to Select,
    customComparator: ()=>{}, // a custom function using which user wants to sort the items. default is undefined and Array.sort() will be used in that case,
    limitTo: 5, // a number thats limits the no of options displayed in the UI similar to angular's limitTo pipe
    moreText: 'more', // text to be displayed whenmore than one items are selected like Option 1 + 5 more
    noResultsFound: 'No results found!', // text to be displayed when no items are found while searching
    searchPlaceholder:'Search', // label thats displayed in search input,
    searchOnKey: 'name' // key on which search should be performed this will be selective search. if undefined this will be extensive search on all keys
    }

  constructor(private route: ActivatedRoute, private bookData: BookDataService) { }

  ngOnInit(): void {
    this.sub.add(
      this.route.params.subscribe(
        (params: {isbn: string}) => {
          this.bookData.getBookByISBN(params.isbn).subscribe(
            (book)=>{this.book = book; this.selectedOptions = this.book.keywords}
          );
        }
      )
    );


}

  update() {
    // ignores input because it is already on book - this method would need parameter for a reactive form
    // in bookmonkey api us updatebyisbn here
    this.bookData.update(this.book).subscribe(() => {this.updateCallBack()});
  }

  updateCallBack(){
    this.message = "Saved!"
    setTimeout(()=>{this.message=""}, 3000)
  }

  ngOnDestroy(): void {
    if (this.sub){
      this.sub.unsubscribe();
    }
  }

  // (change)="changeValue($event)" (open)="open($event)"
  changeValue($event: any) {
    console.log("value changed");
  }

  open($event) {
    console.log("open");

  }


}
