import { Component, OnInit, OnDestroy } from '@angular/core';
import { BookDataService } from 'src/app/book-data.service';
import { Book } from '../book';
import { Observable, Subscription, Subject } from 'rxjs';
import { tap, map, filter, debounceTime, distinctUntilChanged, switchMap } from 'rxjs/operators';


@Component({
  selector: 'app-book-list',
  templateUrl: './book-list.component.html',
  styleUrls: ['./book-list.component.scss']
})


export class BookListComponent implements OnInit {


  sub: Subscription = new Subscription(); // dient als SammelSubskription - kann children über add haben
  subscription: Subscription;
  books: Book[] = [];
  // Opt A und B
  books$: Observable<Book[]>; // BEST OPTION wird nach oben
  // get books() { return this.bookData.findBooks()} // problem zieht zu häufig an von der Rest-Api
  keyUp$ = new Subject<string>();
  isLoading = false;

  constructor(private bookData: BookDataService){}

  ngOnInit(): void {
    const source: Observable<Book[]> = this.bookData.getBooks();
    this.subscription = source.subscribe(x => {
     this.books=x;
      // this.subscription.unsubscribe(); // Opt A
      // this.subscription = null; // OptA
      this.sub.add(this.subscription)}); // sammelsubscription Opt B // tap( console.log)
      // this.books$ = this.bookData.findBooks("");


      this.keyUp$.pipe(
        debounceTime(500),
        distinctUntilChanged(),
        tap(() => this.isLoading = true),
        switchMap(searchTerm => this.bookData.findBooks(searchTerm)),
        tap(() => this.isLoading = false)
      )
      .subscribe(books => this.books = books);
    }


      // if (this.books$ != null){
      //   this.books$.subscribe(x => {this.books = x })
      //   console.log(this.books)
      // }
      // .pipe(map(booksSrc => booksSrc.filter(book => book.title.length >=1)) )
    // });
    // this.books = this.bookData.findBooks(); // "eigentlicher Constructor wegen life-cycle hook" - heavy
    //
  }

  // OPT B - Destruction über Sammel-Subscription
  // ngOnDestroy(): void {
  //   if (this.sub){
  //     this.sub.unsubscribe();
  //   }
  // }

